// 어떤 데이터를 넣을거야 -> 퀴즈 목록 / 유저 정답 목록
// 어떻게 수정 해볼거야 -> 유저가 선택한 오엑스 정답을 정답 목록에 추가해줄거야!

const ADD_ANSWER = "quiz/ADD_ANSWER";

export const addAnswer = (user_answer) => {
    return {type: ADD_ANSWER, user_answer};
};

const initialState = {
    quiz_name: "La’eeb",
    quiz_list: [
        { question: "La'eeb is an Arabic word meaning super-skilled player.", answer: true},
        { question: "La'eeb is the mascot of FIFA(Federation Internationale de Football Association) World Cup 2022 in Qatar.", answer: true},
        { question: "Qatar's FIFA World Cup is the first World Cup ever to be held in the Arab world.", answer: true},
        { question: "2022 FIFA World Cup took place between June and July in summer.", answer: false},
        { question: "The tournament of 2022 FIFA World Cup is set to be a 48 teams.", answer: false},
        { question: "The champions of 2022 FIFA World Cup is South Korea.", answer: false},
    ],
    user_answer_list: [],
};

export default function reducer(state = initialState, action ={}) {
    switch(action.type) {
        case "quiz/ADD_ANSWER": {
            console.log(action);
            const new_user_answer_list = [...state.user_answer_list, action.user_answer];
            
            console.log(new_user_answer_list);
            return {...state, user_answer_list: new_user_answer_list};
        }

        default:
            return state;
    }
};